import { Field, InputType } from "@nestjs/graphql";
import { Type } from "class-transformer";
import { IsBoolean, IsEnum, IsNotEmpty, IsNumber, Max, Min, ValidateNested } from "class-validator";
import { PaypalSetupFeeFailureAction } from "../../../../../orm-config/entity/enum/paypal.setup-failure-action";
import { PaypalSetupFee } from "./paypal-setup-fee";


@InputType()
export class PaypalPaymentPreferences {

  @Field(() => Boolean)
  @IsNotEmpty()
  @IsBoolean()
  autoBillOutstanding: boolean;

  @Field(() => PaypalSetupFee)
  @Type(() => PaypalSetupFee)
  @ValidateNested()
  setupFee: PaypalSetupFee;

  @Field(() => PaypalSetupFeeFailureAction)
  @IsEnum(PaypalSetupFeeFailureAction)
  setupFeeFailureAction: PaypalSetupFeeFailureAction;

  @Field(() => Number)
  @IsNotEmpty()
  @IsNumber()
  @Min(1)
  @Max(999)
  paymentFailureThreshold: number;
  
}
